import { getDefaultOptions, request } from 'api/helpers';

const resource = 'api/vectors';

export const apiVectorGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiVectorPost = async (serviceUrl, vector) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: vector ? JSON.stringify(vector) : null,
  };
  return request(url, options);
};

export const apiVectorPut = async (serviceUrl, id, vector) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: vector ? JSON.stringify(vector) : null,
  };
  return request(url, options);
};

export const apiVectorDelete = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};
